* {
  margin: 0;
  padding: 0;
}
.example {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  max-width: 960px;
  padding: 16px;
}

.example code {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
  background-color: #009688;
  color: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}

.example .background {
  height: 240px;
  // Путь к изображениям из SASS-файла всегда такой, как из папки css
  background-image: url('../images/demo.jpg');
  background-size: cover;
  background-position: center;
}

.form-container {
  display: flex;
  justify-content: center;
  background-color: rgb(21, 74, 143);
  padding-top: 13px;
  padding-bottom: 13px;
  box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.3);
  // position: fixed;
}

.search-input {
  border-radius: 4px;
  width: 200px;
}
.search-form__btn {
  border-radius: 4px;
  background-color: #fff;
  color: #000;
  text-decoration: none;
  color: #fff;
  font-size: 15px;
  padding: 2px 5px 2px 5px;
}

.search-form__btn span {
  color: #000;
}

//GALLERY
.gallery {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 30px 30px 0;
}
.gallery__item {
  list-style: none;
  border: 1px solid #eeeeee;
  flex-basis: calc(100% / 4 - 30px);
  margin-left: 30px;
  margin-top: 30px;
}
.gallery__link {
  text-decoration: none;
  color: #000;
  display: block;
}
.card-thumb {
  width: 100%;
  height: 250px;
}
.card-thumb__img {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  display: block;
}
.card-contetnt {
  padding: 10px;
  display: flex;
  text-align: center;
  font-size: 14px;
}

.gallery-info {
  display: flex;
  flex-direction: row;
}

// //button load-more
.load-more-container {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}
.load-more-btn.is-hidden {
  display: none;
}
.load-more-btn {
  height: 42px;
  border: none;
  background: #3e56ae;
  color: #f2f2f2;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
}

.load-more-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 42px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  transition: all 1s ease;
}

.load-more-btn:hover:before {
  width: 100%;
}
